.preloader {
  background-color: #3757f7;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 1000;
  overflow: hidden;
}

.preloader .angular-shape {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  transform: rotate(50deg) scale(1, 3.5);
}

.preloader .angular-shape > div:first-child {
  background: #3253f7;
  width: 20%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.preloader .angular-shape > div:nth-child(2) {
  background: #3253f7;
  width: 30%;
  height: 100%;
  position: absolute;
  left: 35%;
  top: 0;
}

.preloader .angular-shape > div:nth-child(3) {
  background: #3253f7;
  width: 20%;
  height: 100%;
  position: absolute;
  left: 80%;
  top: 0;
}

.preloader .spinner {
  width: 80px;
  height: 80px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.preloader .double-bounce1,
.preloader .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #fff;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}

.preloader .double-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

@-webkit-keyframes sk-bounce {
  0%, 100% {
    -webkit-transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
  }
}
@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}
